import { SelectColumnsComponent } from './group-header-select-columns.component';
import { Component, OnInit, Input, ViewChild, Injector, Output, EventEmitter, OnChanges, ViewEncapsulation } from '@angular/core';
import { TreeTableComponent, RowNode, TreeNode } from '@farris/ui-treetable';
import { BsModalService } from '@farris/ui-modal';
import { GroupHeaderFormComponent } from './group-header-form.component';
import { cloneDeep } from 'lodash-es';

@Component({
    selector: 'header-group-editor',
    templateUrl: 'header-group-editor.component.html',
    styles: [`
    .farris-treetable .farris-treetable-tbody td .treetable-toggler {
        vertical-align: middle;
    }
    .farris-treetable .farris-treetable-tbody td .treenode-icon {
        margin-right: 2px
    }
    `],
    encapsulation: ViewEncapsulation.None
})
export class HeaderGroupEditorComponent implements OnInit {

    @Input() fields: any[];
    @Input() idField = 'id';
    @Input() treeData = [];

    @ViewChild('tt') tt: TreeTableComponent;

    @Output() selectedNode = new EventEmitter();

    // preview group columns
    groupCols = [];

    HOU_XUN_LIE_ID = '__HOU_XUN_LIE_ID__';

    constructor(public injector: Injector, private modalService: BsModalService) {
    }

    ngOnInit() {
        if (!this.treeData || !this.treeData.length) {
            this.initTreeData();
        } else {
            // å¤è¡¨å¤´ä¸­ç°å­çå­æ®µIDéå
            const currentFieldIds = this.treeData.filter(n => n.fieldRef).map(n => n.fieldRef);
            // æ°å¢çå­æ®µ
            const newFields = this.fields.filter(n => !currentFieldIds.includes(n.id));
            // æ°å¢åéå­æ®µ
            if (newFields && newFields.length) {
                this.treeData = this.treeData.concat(newFields.map(n => {
                    return {
                        fieldRef: n.id,
                        parent: '__HOU_XUN_LIE_ID__',
                        level: 1
                    };
                }));
            }
            this.treeData = this.treeData.map(n => {
                if (n.fieldRef) {
                    const f = this.fields.find(m => m.id === n.fieldRef);
                    if (f) {
                        return {
                            ...f,
                            parent: n.parent
                        };
                    }
                } else {
                    return n;
                }
            }).filter(n => n);

            this.treeData = this.convert2TreeNodes(this.treeData);
        }


        this.tt.removed.subscribe(rowNode => {
            if (!rowNode.node.data.groupHeader) {
                const hxl = this.getHxl();

                if (!rowNode.parent) {
                    this.treeData = this.treeData.filter(n => n.data.id !== rowNode.id);
                }

                this.tt.append(rowNode.node.data, hxl.node);
            }
        });
    }

    initTreeData() {
        this.treeData = [{
            data: { id: 'groupHeader_1', caption: 'åç»è¡¨å¤´1', halign: 'left', groupHeader: true },
            children: this.fields.map((n, i) => {
                n['index'] = i;
                return {
                    data: n,
                    children: [],
                    visible: true
                };
            }),
            expanded: true, visible: true
        }, {
            data: { id: this.HOU_XUN_LIE_ID, caption: '(åéå)', houxunquNode: true },
            children: [],
            expanded: true
        }];
    }

    getHxl() {
        return this.tt.findRowNode(this.HOU_XUN_LIE_ID);
    }

    onMouseEnter($event: MouseEvent) {
        ($event.target as any).querySelector('.rbtn').style.display = 'inline-block';
    }

    onMouseLeave($event: MouseEvent) {
        ($event.target as any).querySelector('.rbtn').style.display = 'none';
    }


    // ç§»é¤å
    removeColumn(event: MouseEvent, rowData: any) {
        this.tt.remove(rowData.id);
    }

    // ç¼è¾åç»å
    editGroupName(rowData: any) {
       const modalRef = this.showDialog(() => {
            const ghfRef = modalRef.content as GroupHeaderFormComponent;
            if (ghfRef.title) {
                rowData.caption = ghfRef.title;
                modalRef.close();
            } else {
                alert('è¯·è¾å¥åç§°ï¼å¦åç¹åæ¶ã');
            }
       });

       modalRef.content.title = rowData.caption;
    }


    private generateGroupHeaderId() {
        return `groupHeader_${new Date().getTime()}`;
    }

    // æ°å¢åç»
    addNewGroup(event: MouseEvent, rn: RowNode, child = false) {
        const modalRef = this.showDialog(() => {
            const ghfRef = modalRef.content as GroupHeaderFormComponent;
            if (ghfRef.title) {
                if (!rn) {
                    this.treeData.splice(0, 0, {
                        data: {
                            id: this.generateGroupHeaderId(), halign: 'left',
                            caption: ghfRef.title, groupHeader: true
                        },
                        icon: 'f-icon f-icon-aggregate-fields',
                        children: []
                    });
                    this.tt.updateSerializedValue();
                } else {
                    this.createNewGroupHeader(rn, ghfRef.title, child);
                }

                modalRef.close();
            } else {
                alert('è¯·è¾å¥åç§°ï¼å¦åç¹åæ¶ã');
            }
       });
    }

    setColumn2Group(event: MouseEvent, col) {
        const i = this.treeData.length - 1;
        this.treeData.splice(i, 0, { data: col, children: [] });

        const hxl = this.treeData.find(n => n.data.houxunquNode);
        hxl.children = hxl.children.filter(n => n.data.id !== col.id);

        this.tt.updateSerializedValue();
    }

    // å é¤åç»
    removeGroup(event: MouseEvent, rowNode) {
        const childs = this.tt.serializedValue.filter(n => n.parents.indexOf(rowNode.id) > -1 && !n.node.data.groupHeader).map(n => n.node);
        this.treeData = this.treeData.filter(n => n.data.id !== rowNode.id);
        this.tt.remove(rowNode.id);
        this.tt.appendChildren(childs, this.getHxl().node);
    }

    setColumns(event: MouseEvent, rowNode) {

        const childs =  rowNode.node.children ? rowNode.node.children.map(n => n.data) : [];

        const modalRef = this.showSelectColumnsDialog(() => {
            const scRef = modalRef.content as SelectColumnsComponent;
            let needAppendNodes = scRef.checkeds;

            const hxl = this.tt.findRowNode(this.HOU_XUN_LIE_ID);
            hxl.node.children = (hxl.node.children || []).filter(n => needAppendNodes.findIndex(c => c.id === n.id) === -1);

            rowNode.node.children = rowNode.node.children || [];
            needAppendNodes = needAppendNodes.filter(n => rowNode.node.children.findIndex(c => c.id === n.id) === -1);
            if (needAppendNodes && needAppendNodes.length) {
                this.tt.appendChildren(needAppendNodes.map(n => {
                    return {
                        data: n,
                        children: [],
                        expanded: true
                    };
                }), rowNode.node);

                this.tt.expandNode(rowNode.id);
            }


            if (scRef.uncheckeds && scRef.uncheckeds.length) {
                // hxl.node.children =  [];

                const hxlCols = scRef.uncheckeds.map(n => {
                    return {
                        data: n,
                        children: []
                    };
                });

                hxl.node.children = hxl.node.children.filter(n => scRef.uncheckeds.findIndex(c => c.id === n.id) === -1);
                this.tt.appendChildren(hxlCols, hxl.node);
            }

            modalRef.close();

        }, childs);
    }

    hasGroupHeader(rowNode: RowNode = null) {
        if (!rowNode) {
            return this.treeData.filter(n => n.groupHeader).length;
        } else {
            return rowNode.node.children.filter(n => n.data.groupHeader).length;
        }
    }

    moveUp(event: MouseEvent, rowNode) {
        let data = this.treeData;
        if (rowNode.parent) {
            data = rowNode.parent.children;
        }

        const index = data.findIndex(n => n.data.id === rowNode.id);
        if (index !== 0) {
            const tempArr = data.splice(index, 1);
            data.splice(index - 1, 0, ...tempArr);

            this.tt.updateSerializedValue();
        }
    }

    moveDown(event: MouseEvent, rowNode) {
        let data = this.treeData;
        if (rowNode.parent) {
            data = rowNode.parent.children;
        }
        const index = data.findIndex(n => n.data.id === rowNode.id);
        const len = data.length;

        if (index !== len - 2) {
            const tempArr = data.splice(index, 1);
            data.splice(index + 1, 0, ...tempArr);
            this.tt.updateSerializedValue();
        } else {
            this.moveBottom(event, rowNode);
        }
    }

    moveTop(event: MouseEvent, rowNode) {
        let data = this.treeData;
        if (rowNode.parent) {
            data = rowNode.parent.children;
        }
        const index = data.findIndex(n => n.data.id === rowNode.id);
        data.unshift(rowNode.node);
        data.splice(index + 1, 1);
        this.tt.updateSerializedValue();
    }

    moveBottom(event: MouseEvent, rowNode) {
        let data = this.treeData;
        if (rowNode.parent) {
            data = rowNode.parent.children;
        }

        const index = data.findIndex(n => n.data.id === rowNode.id);
        data.push(rowNode.node);
        data.splice(index, 1);

        if (!rowNode.parent) {
            this.moveUp(event, rowNode);
        } else {
            this.tt.updateSerializedValue();
        }
    }

    /** è·ååéå */
    getHouXuanLie() {
        return this.treeData.find(n => n.data.houxunquNode).children;
    }

    getAllNodes() {
        const hxl = this.getHouXuanLie();
        return this.tt.serializedValue.filter(n => {
            return !n.node.data.houxunquNode && hxl.findIndex(k => k.data.id === n.id) === -1;
        });
    }


    private convert2TreeNodes(data: any[], parentId = null): TreeNode[] {
        if (data.length) {
            const nodes = data.filter((v) => v.parent === parentId).filter(n => n);
            return nodes.map(item => {
                const node: any = {
                    data: item,
                    children: [],
                    expanded: true
                };

                node.children = this.convert2TreeNodes(data, item.id);
                if (node.data.groupHeader && node.children.length === 0) {
                    node.icon = 'f-icon f-icon-aggregate-fields';
                }
                return node;
            });
        }
        return [];
    }

    private flatNodes(nodes) {
        let r = {};
        nodes.forEach(e => {
            r[e.data['id']] = e.data;
            if (e.children && e.children.length) {
                r = Object.assign(r, this.flatNodes(e.children));
            }
        });
        return r;
    }

    convert2Array(treeNodes: any[]) {
        const r = [];
        const maxBy = (arr, fn) => Math.max(...arr.map(typeof fn === 'function' ? fn : val => val[fn]));
        const maxlevel = maxBy(treeNodes, (n) => n.level) ;
        for (let i = 0; i <= maxlevel; i++ ) {
            r.push(treeNodes.filter(n => n.level === i).map(n => {
                const data = n.node.data;
                return {
                    colspan: (n.node.children ? treeNodes.filter(k => k.parents.indexOf(n.id) > -1 && k.id.indexOf('groupHeader_') == -1).length : 1) || 1,
                    rowspan: (n.node.children && n.node.children.length > 0) ? 1 : (maxlevel - n.level + 1),
                    title: data.caption,
                    field: data.dataField,
                    width: (data.size ? data.size.width : 100),
                    halign: data.halign,
                    align: data.align,
                    id: n.id
                };
            }));
        }

        return r;
    }

    onTreeSelected($event) {
        this.selectedNode.emit($event.node);
    }


    private createNewGroupHeader(rn: RowNode, title: string, child = false) {
        const ghid = this.generateGroupHeaderId();
        const data = { id: ghid, caption: title, groupHeader: true, halign: 'left' };
        const gh = {
            data,
            children: [],
            expanded: true,
            visible: true,
            icon: 'f-icon f-icon-aggregate-fields',
        };
        if (!child) {
            if (!rn.parent) {
                const i = this.treeData.findIndex(n => n.data.id === rn.id) + 1;
                this.treeData.splice(i, 0, gh);
                this.treeData = this.treeData.slice();
            } else {
                this.tt.appendChildren([gh], rn.parent);
            }
        } else {
            this.tt.appendChildren([gh], rn.node);
        }
    }

    private showSelectColumnsDialog(okCallback, childs) {
        const modalRef = this.modalService.show(SelectColumnsComponent, {
            title: 'è®¾ç½®åç»å',
            width: 500,
            height: 300,
            showMaxButton: false,
            resizable: false,
            initialState: {
                fields: this.fields,
                checkeds: childs,
                groupEditorRef: this
            },
            buttons: [
                {
                    text: 'ç¡®å®',
                    cls: 'btn btn-primary',
                    handle: okCallback
                },
                {
                    text: 'åæ¶',
                    cls: 'btn btn-secondary',
                    handle: () => {
                        modalRef.close();
                    }
                }
            ]
        });

        return modalRef;
    }

    private showDialog(okCallback) {
        const modalRef = this.modalService.show(GroupHeaderFormComponent, {
            title: 'åç»ååç§°',
            width: 300,
            height: 200,
            showMaxButton: false,
            resizable: false,
            initialState: {
                title: 'åç»è¡¨å¤´'
            },
            buttons: [
                {
                    text: 'ç¡®å®',
                    cls: 'btn btn-primary',
                    handle: okCallback
                },
                {
                    text: 'åæ¶',
                    cls: 'btn btn-secondary',
                    handle: () => {
                        modalRef.close();
                    }
                }
            ]
        });

        return modalRef;
    }
}
