import { BsModalService } from '@farris/ui-modal';
import { HeaderGroupEditorComponent } from './header-group-editor.component';
import { Component, OnInit, Input, ViewChild, EventEmitter, Output, TemplateRef,
                ElementRef, Renderer2, Injector, HostBinding } from '@angular/core';
import { MessagerService } from '@farris/ui-messager';
import { flatten } from 'lodash-es';

@Component({
    selector: 'column-group-editor',
    templateUrl: 'column-group-editor.component.html',
    styles: [
        `
        .center::-webkit-scrollbar { width: 5px; height: 5px}
        .south::-webkit-scrollbar { width: 5px; height: 5px }
        .south {
            height: 40px;
            min-height: 40px;
            overflow: hidden;
            border-top: 1px solid #c8c8c8;
            background: #F3F8FF;
            transition: height 0.5s;
            flex-shrink: 0!important;;
        }
        .preview-open {
            height: 300px;
        }
        .preview-container {
            flex-shrink: 0!important;overflow:auto
        }
        .preview-title {
            height:36px;min-height: 36px; background: #F3F8FF; cursor:pointer;
        }
        .preview-title .text {
            line-height: 36px;font-size: 16px;padding-left: 10px;
        }
        .preview-title button {
            float: right;
            line-height: 40px;
            margin-right: 10px;
        }
        .preview-container table{
            width: max-content; margin: 5px; background-color: #efefef; border-color: #adadad;
        }
        .preview-container table td{border: 1px solid #c2c2c2;}

        .east.property-column {
            width: 300px; min-width:260px;border-left: 1px solid #c8c8c8;overflow: auto;background:#F3F8FF
        }
        .close-btn {
            display: inline-block;
            float: right;
            line-height: 36px;
            margin-right: 10px;
            width: 36px;
            color: #ccc;
            text-align: center;
        }
        .close-btn:hover {
            color: red;
        }
        `
    ]
})

export class ColumnGroupEditorComponent implements OnInit {
    @Input() editorParams: any = {};
    @Input() fields: any[];
    @Input() value: any[];


    @HostBinding('class') cls = 'f-utils-absolute-all';

    @Output() closeModal = new EventEmitter<any>();
    // æ¨¡ææ¡ç¡®å®åå³é­ï¼å¹¶ä¼ éåæ°
    @Output() submitModal = new EventEmitter<any>();

    @ViewChild('hge') hge: HeaderGroupEditorComponent;
    @ViewChild('dlgFooter') modalFooter: TemplateRef<any>;
    @ViewChild('preview') previewEl: ElementRef;
    @ViewChild('code') codeTemplate: TemplateRef<any>;

    previewGroupColumns = [];
    columnsCode = [];

    btnTopActive = true;
    btnBottomActive = true;

    isOpen = false;
    fieldInfo: any = {};


    modalConfig = {
        title: 'å¤è¡¨å¤´ç¼è¾å¨',
        width: 700,
        height: 500,
        showButtons: true
    };

    constructor(
        private render: Renderer2, private messagerSer: MessagerService, public injector: Injector,
        private modalSer: BsModalService) { }

    ngOnInit() {
        if (!this.fields || !this.fields.length) {
            if (this.editorParams && this.editorParams.fields  && this.editorParams.fields.length) {
                this.fields = this.editorParams.fields;
            } else {
                console.warn('Pleasy Set Datagrid\'s fields Give it 2 me.');
            }
        }
    }

    onTreenodeSelect($event) {
        this.fieldInfo = $event.data;
    }

    private setPreviewColumns() {
        const treeNodes = this.hge.getAllNodes();
        this.previewGroupColumns = this.hge.convert2Array(treeNodes);
    }

    showPreview() {
        this.setPreviewColumns();
        if (!this.isOpen) {
            this.isOpen = true;
        }
    }

    closePreview(event?: MouseEvent) {
        this.isOpen = false;
        if (event) {
            event.stopPropagation();
        }
    }

    resetGroupColumns() {
        this.messagerSer.question('è¡¨å¤´åç»éç½®åéè¦éæ°è®¾ç½®ãç¡®å®è¦éç½®ä¸ºåå§ç¶æåï¼', () => {
            this.hge.initTreeData();
        });
    }

    cancle() {
        this.closeModal.emit();
    }

    ok() {
        this.setPreviewColumns();

        const flatgroupcols = flatten<any>(this.previewGroupColumns);

        const value = this.hge.tt.serializedValue.map(n => {
            const parent = n.parent ? n.parent.data.id : null;
            if (n.node.data.groupHeader || n.node.data.houxunquNode) {
                const _col = flatgroupcols.find(c => c.id === n.id);
                const item = {
                    ...n.node.data,
                    parent,
                    level: n.level
                };

                if (_col) {
                    return Object.assign(item, { rowspan: _col ? _col.rowspan : 1,
                        colspan: _col ? _col.colspan : 1});
                }

                return item;

            } else {
                return {
                    fieldRef: n.node.data.id,
                    parent,
                    level: n.level
                };
            }
        });
        this.submitModal.emit({ value });
    }

    showCode() {
        const treeNodes = this.hge.getAllNodes();
        this.columnsCode = this.hge.convert2Array(treeNodes);

        this.modalSer.show(this.codeTemplate, {
            title: 'è¡¨å¤´åç»ä»£ç ',
            width: 500, height: 400,
            resizable: false,
            showMaxButton: false,
            showButtons: false
        });
    }
}
