import { HeaderGroupEditorComponent } from './header-group-editor.component';
import { Component, OnInit, Input, ChangeDetectorRef } from '@angular/core';

@Component({
    selector: 'select-columns',
    template: `
    <div style="margin: 10px 20px" class="row">
        <div class="custom-control custom-checkbox col-4" *ngFor="let f of fields">
            <input type="checkbox" class="custom-control-input" [checked]="isChecked(f)" [disabled]="isDisabled(f)">
            <label class="custom-control-label" (click)="checkedCols(f)" >{{ f.caption }}</label>
        </div>
    </div>
    `
})
export class SelectColumnsComponent implements OnInit {
    @Input() fields = [];

    checkeds = [];
    uncheckeds = [];
    childs = [];
    groupEditorRef: HeaderGroupEditorComponent;
    private checkedPropertyName = '__checked__';
    // åéå
    private hxl = [];
    constructor(private cd: ChangeDetectorRef) { }

    ngOnInit(): void {
        this.initCheckedProperty();

        this.childs = Array.from(this.checkeds);

        if (this.groupEditorRef) {
            this.hxl = this.groupEditorRef.getHouXuanLie();
            if (this.hxl.length) {
                this.hxl = this.hxl.map(n => n.data);
            }
        }
    }

    private initCheckedProperty() {
        if (!this.checkeds) {
            this.checkeds = [];
            this.fields.forEach(f => {
                f[this.checkedPropertyName] = false;
            });
        } else {
            this.fields.forEach(f => {
                f[this.checkedPropertyName] = this.isChecked(f);
            });
        }
    }

    checkedCols(f) {

        if (this.isDisabled(f)) {
            return;
        }

        const checked = this.isChecked(f);
        f[this.checkedPropertyName] = !checked;

        const _fields = this.fields.filter(n => !this.isDisabled(n));

        this.checkeds = _fields.filter(n => n[this.checkedPropertyName]);
        this.uncheckeds = _fields.filter(n => !n[this.checkedPropertyName] );

        this.cd.detectChanges();
    }

    isChecked(f) {
        let chk = false;
        if (this.checkeds && this.checkeds.length) {
            chk = this.checkeds.findIndex(n => n.id === f.id) > -1;
        }

        return chk;
    }

    /** å¨å¶ä»åç»ä¸­çåï¼ç¦ç¨éæ©
     * ä»å½ååç»ä¸­çåååéåä¸­çé¡¹å¯ä»¥éæ©
     */
    isDisabled(f) {
        let items = this.childs;
        if (this.hxl && this.hxl.length) {
            items = items.concat(this.hxl);
        }

        return !(items.findIndex(n => n.id === f.id) > -1);
    }
}
